<template>
    <div class="attention_of_teacher">
        <div class="attention_of_header">
            <div class="attention_of_header_left">
                 <van-icon name="arrow-left" @click="rollBackMymine"/>
            </div>
            <div class="attention_of_header_right">我的关注</div>
        </div>
        <div class="attention_of_content">
            <div class="attention_of_content_info" v-for="(item,index) in attentionData.list" :key="index">
                <div class="attention_of_content_info_img">
                    <img :src="item.avatar"/>
                </div>
                <div class="attention_of_content_info_name">
                    <div class="attention_of_content_info_top">
                        <font>{{item.teacher_name}}</font>
                        <span style="color:red; fontSize:0.85rem">{{item.level_name}}</span>
                    </div>
                    <div class="attention_of_content_info_bottom">
                       <div>这是一段很长的文字，当容器放不下则会出现省略号。这是一段很长的文字，当容器放不下则会出现省略号。这是一段很长的文字，当容器放不下则会出现省略号。</div>
                    </div>
                </div>
                <div class="attention_of_content_info_btn">
                    <button @click="cancelClick">{{attention}}</button>
                </div>
            </div>
        </div>
        <div class="attention_of_teacher_end">
            没有更多了
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
          attention:"取消关注",
            attentionData: [],
            collectID: Number
        }
    },
    methods: {
        rollBackMymine(){
            this.$router.go(-1)
        },
        cancelClick() {
      this.$http.put(`/api/app/collect/cancel/${this.collectID}/2`).then(resp => {
          console.log(resp.data);
          this.$http.get("/api/app/collect?page=1&limit=10&type=2&").then(resp => {
              resp.data.data.list.forEach(ele => {
                this.collectID = ele.collect_id;
              });
              this.attentionData = resp.data.data;
            });
        });
    },
    },
    mounted() {
    this.$http.get("/api/app/collect?page=1&limit=10&type=2&").then(resp => {
      console.log(resp.data.data.list);
      resp.data.data.list.forEach(ele => {
        this.collectID = ele.collect_id;
      });
      this.attentionData = resp.data.data;
    });
  }
}
</script>
<style>
.attention_of_teacher
{
    width: 100%;
    height: 667px;
    background: rgb(238, 237, 237);
}
   .attention_of_header
   {
       width: 100%;
       height: 3.5rem;
       background: white;
       display: inline-flex;
       justify-content: center;
       align-items: center;
       color: rgb(94, 93, 93);
       font-size: 1.2rem;
   } 
   .attention_of_header_left
   {
        width: 2.5rem;
        display: inline-flex;
       justify-content: center;
       align-items: center;
   }
   .attention_of_header_right
   {
         flex: 1;
        display: inline-flex;
       justify-content: center;
       align-items: center;
       margin-right: 2rem;
   }
   .attention_of_content
   {
       width: 93%;
       margin: 1rem auto 0;
   }
   .attention_of_content_info
   {
       width: 100%;
       height: 5rem;
       background: white;
       border-radius: 0.3rem;
       display: inline-flex;
       justify-content: center;
       align-items: center;
       margin-bottom: 0.6rem;
   }
   .attention_of_content_info_img,.attention_of_content_info_name,
   .attention_of_content_info_btn
   {
      display: inline-flex;
      justify-content: center;
      align-items: center;
   }
   .attention_of_content_info_img
   {
       width: 15%;
       height: 5rem;
       /* background: rgb(36, 180, 190); */
   }
   .attention_of_content_info_img img
   {
       width: 100%;
   }
   .attention_of_content_info_btn
   {
       width: 25%;
       height: 5rem;
       /* background: chartreuse; */
   }
   .attention_of_content_info_btn button
   {
       width: 80%;
       height: 1.7rem;
       background: rgb(247, 215, 221);
       border: 0;
       border-radius: 5rem;
       color: orangered;
       font-size: 0.85rem;
   }
   .attention_of_content_info_name
   {
       width: 60%;
       height: 5rem;
       /* background: rgb(255, 0, 179); */
       display: inline-flex;
       justify-content: center;
       align-items: center;
       flex-wrap: wrap;
   }
   .attention_of_content_info_bottom{
       width: 100%;
       /* background: chartreuse; */
   }
    .attention_of_content_info_bottom div{
       width: 95%;
        display: block;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-size: 0.85rem;
        padding-left: 1rem;
        color: gray;
   }
   .attention_of_content_info_top
   {
       width: 100%;
       /* background: chocolate; */
       height: 1rem;
       display: inline-flex;
       justify-content:flex-start;
       align-items: center;
   }
   .attention_of_teacher_end
   {
       width: 100%;
       height: 4rem;
       display: inline-flex;
       justify-content: center;
       align-items: center;
       color: grey;
       font-size: 0.9rem;
   }

</style>